
import { defineComponent } from 'vue'
import ViewPort from '../DataViews/ViewPort.vue'
import { modalController } from '@ionic/vue'
import { Option } from '@/components/Forms/FieldInterface'
import KeyPad from '../Keyboard/HisKeypad.vue'
import FieldMixinVue from './FieldMixin.vue'
import {
    IonGrid,
    IonCol,
    IonRow,
    IonInput
} from "@ionic/vue"
export default defineComponent({
  components: { 
    ViewPort,
    IonGrid,
    IonCol,
    IonRow,
    IonInput
  },
  mixins: [FieldMixinVue],
  data: () => ({
    listData: [] as any
  }),
  watch: {
    clear: {
        handler() {
            this.listData = this.listData.filter((item: Option) => {
                item.value = ''
                return true
            })
        },
        immediate: true
    },
    listData: {
        handler() {
            this.$emit('onValue', this.listData)
        },
        deep: true,
        immediate: true
    }
  },
  async activated() {
    this.$emit('onFieldActivated', this)
    this.listData = await this.options(this.fdata, this.cdata, this.listData)
  },
  methods: {
    async launchKeyPad(item: Option) {
        const modal = await modalController.create({
            component: KeyPad,
            backdropDismiss: false,
            cssClass: 'keypad-modal',
            componentProps: {
                title: item.label,
                preset: item.value,
                strictNumbers: true,
                onKeyPress(val: string){
                    item.value = val
                }
            }
        })
        modal.present()
    }
  }
})
