
import { defineComponent } from 'vue'
import { delayPromise } from "@/utils/Timers";
import {
    IonPage,
    IonContent
} from "@ionic/vue"
export default defineComponent({
    components: {
        IonPage,
        IonContent
    },
    emits: ['onDismissModal'],
    setup(_, {emit}) {
        delayPromise(1500).then(() => {
            emit('onDismissModal')
        })
    },
})
