
import { defineComponent } from 'vue'
import {
    IonCard,
    IonThumbnail,
    IonItem,
    IonLabel
} from "@ionic/vue"
import img from "@/utils/Img"

export default defineComponent({
    components: {
        IonCard, 
        IonThumbnail, 
        IonItem, 
        IonLabel
    },
    props: {
        title: {
            type: String,
            required: true
        },
        icon: {
            type: String,
            required: true
        },
        description: {
            type: String,
            required: false
        },
        taskCompleted: {
            type: Boolean,
            required: false
        }
    },
    setup(props) {
        const checkIcon = img('Checkmark.svg')
        const isCompleted: boolean = typeof props.taskCompleted === 'boolean' 
            ? props.taskCompleted 
            : false;
        return {
            checkIcon,
            isCompleted
        }
    }
})
