
import Img from "@/utils/Img"
import { defineComponent } from "vue";
import {
  IonContent,
  IonItem,
  IonList,
  IonMenu,
  IonButtons,
  IonMenuButton,
  IonTitle,
  IonThumbnail,
  IonToolbar,
  IonHeader,
  IonLabel,
  menuController
} from "@ionic/vue";
import { Option } from "../../Forms/FieldInterface";
export default defineComponent({
  name: "App",
  emits: [ 'onClickMenuItem'],
  props: {
    title: String,
    menuTitle: String,
    menuItems: {
        type: Array,
        default: () => ([])
    },
    appIcon: {
        type: String,
        required: true
    }
  },
  components: {
    IonContent,
    IonItem,
    IonList,
    IonTitle,
    IonMenu,
    IonButtons,
    IonMenuButton,
    IonThumbnail,
    IonToolbar,
    IonHeader,
    IonLabel
  },
  setup(props, context) {
    function onClickItem(item: Option) {
        menuController.close('app-menu')
        context.emit('onClickMenuItem', item)
    }
    return {
      onClickItem,
      appIconPath: Img(props.appIcon)
    }
  },
});
